<template lang="pug">
  .approve-dialog
    el-dialog(:title="title", v-model="dialogVisible",  @open="onDialogOpen")
      el-form(:model='approve', :rules="rules", ref="approveForm", :label-width='formLabelWidth')
        el-form-item(prop="name", label='')
          el-input(v-model='approve.name', auto-cameomplete='off', placeholder='')
        el-form-item(prop="type", label='')
          el-radio.radio(v-model='approve.type', label='') 
          el-radio.radio(v-model='approve.type', label='') 
        el-form-item.is-required(prop="relationProducts", label='')
          input(type="hidden", v-model="approve.relationProducts")
          .relation-products-tag(v-show="approve.relationProducts && approve.relationProducts.length")
            | 
            span(v-for="r in approve.relationProducts")
              | {{r.name}}
              i.icon-batonx.icon-close(@click="removeRelationProduct(r)")
          product-list(:on-change="onProductCheckChange", :checked-list="approve.relationProducts")
        el-form-item(prop="flows", label='')
          .flow(v-for="flow in approve.flowList")
            el-select(v-model="approve.department", placeholder="")
              el-option(v-for="d in departments", :value="d.value", :label="d.name")
            el-select(v-model="approve.job", placeholder="")
              el-option(v-for="j in filterJobs(flow)", :value="j.value", :label="j.name")
            el-select(v-model="approve.approver", placeholder="")
              el-option(v-for="a in filterApprovers(flow)", :value="a.value", :label="a.name")
        el-form-item(label='')
          el-button(type="text")
            i.icon-batonx.icon-plus
            | 
      .dialog-footer(slot="footer")
        el-button(type="primary", size="small", @click='approveSave') 
        el-button(type='gray', size="small", @click='dialogVisible = false') 
</template>

<script>
import ProductList from '@/components/ProductList.vue'
import {
  merge,
  uniqueId,
  filter,
  remove
} from 'lodash'
import {
  validateArray
} from '@/common/validator.js'

export default {
  components: {
    ProductList
  },
  methods: {
    open(approve = {
      name: '',
      type: '',
      flowList: [],
      relationProducts: []
    }) {
      this.dialogVisible = true
      this.$nextTick(() => {
        this.approve = merge({}, this.approve, approve)
        this.title = approve.id ? '' : ''
      })
    },

    onDialogOpen() {
      this.$nextTick(() => this.$refs.approveForm.resetFields())
    },

    removeRelationProduct(product) {
      this.approve.relationProducts = remove(this.approve.relationProducts, v => v.name !== product.name)
    },

    onProductCheckChange(products) {
      this.approve.relationProducts = merge([], products)
    },

    approveSave() {
      this.$refs.approveForm.validate(valid => {
        if (valid) {
          this.dialogVisible = false
          if (!this.approve.id) this.approve.id = uniqueId('approve_')
          this.$emit('approve-save', this.approve)
        }
      })
    },

    filterJobs(flow) {
      if (!flow.department) return []
      const department = find(this.departments, d => d.name === flow.department)
      return filter(this.jobs, j => j.department === department.id)
    },

    filterApprovers(flow) {
      if (!flow.job) return []
      const job = find(this.jobs, d => d.name === flow.job)
      return filter(this.approvers, j => j.job === job.id)
    }
  },

  data() {
    return {
      title: '',
      approve: {
        name: '',
        job: '',
        type: '',
        flowList: [],
        relationProducts: []
      },
      departments: [{
        id: 1,
        name: '',
        value: ''
      }, {
        id: 2,
        name: '',
        value: ''
      }, {
        id: 3,
        name: '',
        value: ''
      }, {
        id: 4,
        name: '',
        value: ''
      }],
      jobs: [{
        id: 1,
        department: 1,
        name: '',
        value: ''
      }, {
        id: 2,
        department: 1,
        name: '',
        value: ''
      }, {
        id: 3,
        department: 1,
        name: '',
        value: ''
      }, {
        id: 4,
        department: 2,
        name: '',
        value: ''
      }, {
        id: 5,
        department: 2,
        name: '',
        value: ''
      }, {
        id: 6,
        department: 2,
        name: '',
        value: ''
      }, {
        id: 7,
        department: 3,
        name: '',
        value: ''
      }, {
        id: 8,
        department: 3,
        name: '',
        value: ''
      }, {
        id: 9,
        department: 3,
        name: '',
        value: ''
      }, {
        id: 10,
        department: 4,
        name: '',
        value: ''
      }, {
        id: 11,
        department: 4,
        name: '',
        value: ''
      }, {
        id: 12,
        department: 4,
        name: '',
        value: ''
      }],
      approvers: [{
        name: '',
        job: 1
      }, {
        name: '',
        job: 2
      }, {
        name: '',
        job: 3
      }, {
        name: '',
        job: 4
      }, {
        name: '',
        job: 5
      }, {
        name: '',
        job: 6
      }, {
        name: '',
        job: 7
      }, {
        name: '',
        job: 8
      }, {
        name: '',
        job: 9
      }, {
        name: '',
        job: 10
      }, {
        name: '',
        job: 11
      }, {
        name: '',
        job: 12
      }],
      rules: {
        name: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        type: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
        relationProducts: [{
          validator: validateArray,
          fieldName: '',
          trigger: 'change'
        }]
      },
      formLabelWidth: '120px',
      dialogVisible: false
    }
  }
}
</script>

<style lang="scss">
@import '../assets/scss/_vars.scss';
.relation-products-tag {
  font-size: 12px;
  span {
    padding: 5px 8px;
    border-radius: 3px;
    background: $border-color;
    margin-right: 10px;
    display: inline-block;
    line-height: 1;
    .icon-close {
      cursor: pointer;
      color: #929aa3;
      margin-left: 10px;
      display: inline-block;
      transform: scale(.7);
      &:hover {
        color: $color-blue;
      }
    }
  }
}
</style>



