<template lang="pug">
  section.approve-list
    .box
      .box-tab-header
        el-button(type="primary", size="small", @click="openApproveDialog()")
          i.icon-batonx.icon-plus
          | 
      el-tabs(v-model='tab', type='card', @tab-click='tabClick')
        el-tab-pane(label='', name='')
          .filters
            el-input(placeholder='', icon='search', v-model.lazy='filter.name')
            el-button(size="small", type="primary", @click="clearFilter")  
        el-tab-pane(label='', name='')
          .filters
            el-input(placeholder='', icon='search', v-model.lazy='filter.name')
            el-button(size="small", type="primary", @click="clearFilter")  
    .table-container
      el-table(:data='filterApproves', style='width: 100%')
        el-table-column(prop='name', label='', width='220')
          template(scope='scope')
            el-popover(v-if="scope.row.note", trigger='hover' placement='top', )
              p : {{ scope.row.note }}
              .name-wrapper(slot='reference')
                | {{ scope.row.name }}
                i.icon-batonx.icon-explain
            span(v-if="!scope.row.note") {{scope.row.name}}
        el-table-column(prop='detail', label='')
        el-table-column(label='', width='100')
          template(scope="scope")
            .operations
              i.icon-batonx.icon-edit(@click="openApproveDialog(scope.row)")
              i.icon-batonx.icon-delete(@click.stop="deleteApprove(scope.row)")
      el-pagination(@size-change='pageSizeChange', @current-change='pageChange', :current-page='page.current', :page-sizes="page.sizes", :page-size="page.size", layout='total, prev, pager, next, jumper', :total='approves.length')
    approve-dialog(ref="approveDialog", @approve-save="onApproveSave")
</template>

<script>
import ApproveDialog from '@/components/ApproveDialog.vue'
import {
  remove,
  merge,
  filter,
  each
} from 'lodash'
import {
  MessageBox
} from 'element-ui'

export default {
  components: {
    ApproveDialog
  },
  methods: {
    tabClick(tab, event) {
      console.log(tab, event)
    },

    clearFilter() {
      each(this.filter, (v, k) => {
        this.filter[k] = ''
      })
    },

    pageChange(val) {
      console.log(`${val}`)
    },

    pageSizeChange(val) {
      this.page.size = val
    },

    openApproveDialog(approve) {
      this.$refs.approveDialog.open(approve)
    },

    onApproveSave(approve) {
      const activeApprove = find(this.approves, v => v.id === approve.id)
      if (activeApprove) {
        merge(activeApprove, approve)
      } else {
        this.approves.unshift(approve)
      }
    },

    deleteApprove(approve) {
      MessageBox.confirm('', '', {
        type: 'warning'
      }).then(() => {
        this.approves = remove(this.approves, (v) => {
          return approve.id !== v.id
        })
      })
    }
  },

  computed: {
    filterApproves() {
      return filter(this.approves, v => {
        return ~v.name.indexOf(this.filter.name) && v.type === this.tab
      })
    }
  },

  data() {
    return {
      tab: '',
      filter: {
        name: ''
      },
      page: {
        current: 1,
        size: 10,
        sizes: [10, 20, 30, 50]
      },
      approves: [{
        id: 1,
        name: 'A1',
        type: '',
        detail: '',
        flowList: [{
          department: '',
          job: '',
          approver: ''
        }, {
          department: '',
          job: '',
          approver: ''
        }, {
          department: '',
          job: '',
          approver: ''
        }],
        relationProducts: [{
          id: 1,
          name: 'A',
          amount: ' 78,613,007.00',
          platform: ''
        }, {
          id: 2,
          name: 'B',
          amount: ' 80,533,201.00',
          platform: ''
        }]
      }, {
        id: 2,
        name: 'A1',
        type: '',
        detail: '',
        flowList: [{
          department: '',
          job: '',
          approver: ''
        }, {
          department: '',
          job: '',
          approver: ''
        }],
        relationProducts: [{
          id: 1,
          name: 'A',
          amount: ' 78,613,007.00',
          platform: ''
        }, {
          id: 2,
          name: 'B',
          amount: ' 80,533,201.00',
          platform: ''
        }]
      }, {
        id: 3,
        name: 'A2',
        type: '',
        detail: '',
        flowList: [{
          department: '',
          job: '',
          approver: ''
        }],
        relationProducts: [{
          id: 1,
          name: 'A',
          amount: ' 78,613,007.00',
          platform: ''
        }, {
          id: 2,
          name: 'B',
          amount: ' 80,533,201.00',
          platform: ''
        }]
      }, {
        id: 4,
        name: 'A2',
        type: '',
        detail: '',
        flowList: [{
          department: '',
          job: '',
          approver: ''
        }, {
          department: '',
          job: '',
          approver: ''
        }, {
          department: '',
          job: '',
          approver: ''
        }],
        relationProducts: [{
          id: 1,
          name: 'A',
          amount: ' 78,613,007.00',
          platform: ''
        }, {
          id: 2,
          name: 'B',
          amount: ' 80,533,201.00',
          platform: ''
        }]
      }, {
        id: 5,
        name: 'A3',
        type: '',
        detail: '',
        flowList: [{
          department: '',
          job: '',
          approver: ''
        }, {
          department: '',
          job: '',
          approver: ''
        }],
        relationProducts: [{
          id: 1,
          name: 'A',
          amount: ' 78,613,007.00',
          platform: ''
        }, {
          id: 2,
          name: 'B',
          amount: ' 80,533,201.00',
          platform: ''
        }]
      }]
    }
  }
}
</script>

